import React from 'react';
import { Helmet } from 'react-helmet';

// import Custom Components
import PageHeader from '../../common/page-header';
import Breadcrumb from '../../common/breadcrumb';
import ShopSidebar from '../../features/sidebar/shop-sidebar';
import ProductList from '../../features/product/list/product-list';
import {  resetFilter} from '../../../actions';
import _data from '../../../mock_data/data.json';
const querySearch = require("stringquery");

function ShopList( props ) {
    let grid = props.match.params.grid;
    let search =  querySearch(props.location.search); 
    let indexFilters =  search!== undefined && search.categoryIndex!== undefined?{categoryIndex:search.categoryIndex}:{}
    console.log("indexFilters "+ JSON.stringify(indexFilters))
    resetFilter()
    var sTitle = "Vanis Store"
    for (let i = 0; i <  _data.categories.wears.length; i++) {
        let element =  _data.categories.wears[i]
        if(element.id === parseInt(indexFilters.categoryIndex, 10)){
            sTitle = [element.name]
            break
        } 
    };
    sTitle = sTitle.toString().replace("Đồ", "Thời Trang")
    const titles = { "list": "List", "2cols": "Grid 2 Columns", "3cols": "Vanis Store", "4cols": "Grid 4 Columns" };

    if ( grid !== "list" && grid !== "2cols" && grid !== "3cols" && grid !== "4cols" ) {
        window.location = process.env.PUBLIC_URL + "/pages/404";
    }

    return (
        <>
            <Helmet>
                <title>VANIS STORE - Đà Nẵng</title>
            </Helmet>

            <h1 className="d-none">VANIS STORE - Đà Nẵng</h1>

            <div className="main">
                <PageHeader title={ sTitle} subTitle="Đồng hành cùng đẳng cấp thời trang" />
                <Breadcrumb title={ sTitle } adClass="mb-2" parent1={ [ "Sản phẩm", "shop/sidebar/3cols" ] }/>

                <div className="page-content">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-9 skeleton-body skel-shop-products">
                                <ProductList column={ grid } indexFilters = {indexFilters}/>
                            </div>

                            <div className="col-lg-3 order-lg-first skeleton-body skel-shop-sidebar">
                                <div className="skel-widget">
                                </div>

                                <div className="skel-widget">
                                </div>

                                <div className="skel-widget">
                                </div>

                                <div className="skel-widget">
                                </div>

                                <ShopSidebar adClass="sidebar sidebar-shop" indexFilters = {indexFilters} />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

export default React.memo( ShopList );