import React, { useEffect,useState } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { Helmet } from 'react-helmet';
import { toast } from 'react-toastify';
import * as api from '../../../api'
// import Custom Components
import PageHeader from '../../common/page-header';
import Breadcrumb from '../../common/breadcrumb';
import Accordion from '../../features/accordion/accordion';
import Card from '../../features/accordion/card';
import { clearCart } from '../../../actions';

import { getCartTotal } from '../../../services';
import ReactLoading from 'react-loading';
function Checkout( props ) {
    const { cartlist, total,clearCart } = props;
    const shippingPrice = { "free": 0, "standard": 10, "express": 20 };
    const shippingObj = { "free": "Free shipping", "standard": "Standard", "express": "Express" };
    const [ errors, setErrors ] = useState( {} );
    const [ loading, setLoading ] = useState( false );
    useEffect( () => {
        let item = document.querySelector( "#checkout-discount-input" );

        var opactiyEffect = function ( e ) {
            e.currentTarget.parentNode.querySelector( "label" ).setAttribute( "style", "opacity: 0" );
        }

        var blurEffect = function ( e ) {
            let $this = e.currentTarget;
            if ( $this.length !== 0 ) {
                $this.parentNode.querySelector( "label" ).setAttribute( "style", "opacity: 0" );
            } else {
                $this.parentNode.querySelector( "label" ).setAttribute( "style", "opacity: 1" );
            }
        }

        item.addEventListener( "focus", opactiyEffect );

        item.addEventListener( "blur", blurEffect )

        return () => {
            item.removeEventListener( "focus", opactiyEffect );

            item.removeEventListener( "blur", blurEffect );
        }
    }, [] )

   const handleValidation = (province,state,house,phone,email,fullName) => {


     let _errors = {}
        let formIsValid = true;
        //Name
        if(fullName.length === 0){
            _errors["fullName"] = "(Không được để trống)";
            formIsValid = false
         }
        if(province.length === 0){
           _errors["province"] = "(Không được để trống)";
           formIsValid = false
        }
        if(state.length === 0){
            _errors["state"] = "(Không được để trống)";
            formIsValid = false
         }
         if(house.length === 0){
            _errors["house"] = "(Không được để trống)";
            formIsValid = false
         }
         if(phone.length === 0){
            _errors["phone"] = "(Không được để trống)";
            formIsValid = false
         }
         if(email.length === 0){
            _errors["email"] = "(Không được để trống)";
            formIsValid = false
         }
         let lastAtPos = email.lastIndexOf('@');
        let lastDotPos = email.lastIndexOf('.');

        if (!(lastAtPos < lastDotPos && lastAtPos > 0 && email.indexOf('@@') == -1 && lastDotPos > 2 && (email.length - lastDotPos) > 2)) {
            
            _errors["email"] = "Email không đúng định dạng";
            formIsValid = false
        }
  
        setErrors(_errors)
       return formIsValid;
   }
   const  makeid = (length)=> {
    var result           = '';
    var characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    var charactersLength = characters.length;
    for ( var i = 0; i < length; i++ ) {
      result += characters.charAt(Math.floor(Math.random() * 
 charactersLength));
   }
   return result.toUpperCase();
}
    const checkoutHandle = () => {
        console.log("checkoutHandle")
        const province = document.querySelector( "#province" ).value
        const state = document.querySelector( "#state" ).value
        const house = document.querySelector( "#house" ).value
        const phone = document.querySelector( "#phone" ).value
        const email = document.querySelector( "#email" ).value
        const note = document.querySelector( "#note" ).value
        const fullName = document.querySelector( "#fullName" ).value
        
        if (cartlist.length== 0){
            toast.error( "Có lỗi xảy ra. Vui lòng kiểm tra giỏ hàng của bạn" ); 
        }else
        if ( handleValidation(province,state,house,phone,email,fullName))
            {
                var arrayItem = []
                 cartlist.map( ( item, _ ) =>{
                   var item =  {
                        "quantity" : parseInt(item.qty, 10),
                        "orderId" : makeid(10),
                        "id" : makeid(10),
                        "size":item.sizeSelect,
                        "color":item.colorSelect,
                        "price" : item.sum,
                        "productId" : item.id,
                        "product" : item,
                        "nameProduct" : item.name,
                        "image":item.pictures[0]
                      
                     }
                     arrayItem.push(item)
                 }
                   
                ) 

                var order = {
                    "orderid":makeid(10),
                    "address" : {
                        "province" : province ,
                        "codeProvince" : "48",
                        "codeState" : "492",
                        "state" : state,
                        "house" : house,
                        
                      },
                      "phone" : phone ,
                      "note" : note ,
                      "userId" : 1,
                      "fullName" : fullName ,
                      "itemAmount" : total,
                      "shippingTotal" : shippingPrice[ props.shipping ],
                      "promoTotal" : 0,
                      "totalAmount" : total+shippingPrice[ props.shipping ],
                      "orderDetails":{
                          "results":arrayItem,
                          "total":arrayItem.length
                      }
                      
                   }
                   setLoading(true)
                   api.createOrder(order).then( results => {
                    setLoading(false)
                    if (results.status===200){
                        clearCart()
                        console.log("goto Home");
                        props.history.push(`${process.env.PUBLIC_URL}/pages/order-completed`)
                    }else{
                        console.log("Home");
                    }
                        } )
                   
            }
            else{
                toast.error( "Có lỗi xảy ra. Vui lòng kiểm tra các trường bắt buộc" );
            }

       
            
    }
    return (
        <>
       

            <Helmet>
                <title>Xác nhận đặt hàng</title>
            </Helmet>

            <h1 className="d-none">Xác nhận đặt hàng</h1>

            <div className="main">

                <PageHeader title="Xác nhận đặt hàng" subTitle="Vui lòng điền đầy đủ thông tin bên dưới." />
                <Breadcrumb title="Xác nhận đặt hàng" />

                <div className="page-content">
                    <div className="checkout">
                        <div className="container">
                            <div className="checkout-discount">
                                <form action="#">
                                    <input type="text" className="form-control" required id="checkout-discount-input" />
                                    <label htmlFor="checkout-discount-input" className="text-truncate">Bạn có mã giảm giá? <span>Nhấn vào đây để nhập mã của bạn</span></label>
                                </form>
                            </div>

                            <form >
                                <div className="row">
                                    <div className="col-lg-9">
                                        <h2 className="checkout-title">Thông tin người nhận</h2>
                                        <label>Họ và tên *</label><span style={{color: "red",marginLeft:10}}>{errors["fullName"]}</span>
                                        <input id="fullName" type="text" className="form-control" placeholder="Họ và tên đầy đủ của bạn" required />
                                        
                                        <label>Số nhà, tên đường *</label><span style={{color: "red",marginLeft:10}}>{errors["house"]}</span>
                                        <input id="house" type="text" className="form-control" placeholder="Số nhà, tên đường, ghi rõ hẻm, kiệt hoặc phòng tầng, khu..." required />
                                        <label>Tỉnh/ Thành phố *</label><span style={{color: "red",marginLeft:10}}>{errors["province"]}</span>
                                        <input id="province" type="text" className="form-control" required />
                                        <label>Quận/ Huyện *</label><span style={{color: "red",marginLeft:10}}>{errors["state"]}</span>
                                        <input id="state" type="text" className="form-control" required />

                                        <div className="row">
                                            <div className="col-sm-6">
                                                <label>Số điện thoại *</label><span style={{color: "red",marginLeft:10}}>{errors["phone"]}</span>
                                                <input id="phone" type="tel" className="form-control" required />
                                            </div>

                                            <div className="col-sm-6">
                                                <label>Địa chỉ email *</label><span style={{color: "red",marginLeft:10}}>{errors["email"]}</span>
                                                <input id="email" type="email" className="form-control" required />
                                            </div>
                                        </div>


                                        <label>Lưu ý cho chủ shop (không bắt buôn)</label>
                                        <textarea id="note" className="form-control" cols="30" rows="4" placeholder=""></textarea>
                                    </div>

                                    <aside className="col-lg-3">
                                        <div className="summary">
                                            <h3 className="summary-title">Đơn Hàng Của Bạn</h3>

                                            <table className="table table-summary">
                                                <thead>
                                                    <tr>
                                                        <th>Sản Phẩm</th>
                                                        <th>Giá</th>
                                                    </tr>
                                                </thead>

                                                <tbody>

                                                    { cartlist.map( ( item, index ) =>
                                                        <tr key={ index }>
                                                            <td><Link to="#">{ item.name }</Link></td>
                                                            <td>{ item.sum.toLocaleString( undefined, { minimumFractionDigits: 0, maximumFractionDigits: 0 } ) } đ</td>
                                                        </tr>
                                                    ) }
                                                    <tr className="summary-subtotal">
                                                        <td>Tổng tiền:</td>
                                                        <td>{ total.toLocaleString( undefined, { minimumFractionDigits: 0, maximumFractionDigits: 0 } ) } đ</td>
                                                    </tr>
                                                    <tr>
                                                        <td>Giao hàng:</td>
                                                        <td>{ shippingObj[ props.shipping ] }</td>
                                                    </tr>
                                                    <tr className="summary-total">
                                                        <td>Tổng:</td>
                                                        <td>{ ( total + shippingPrice[ props.shipping ] ).toLocaleString( undefined, { minimumFractionDigits: 0, maximumFractionDigits: 0 } ) } đ</td>
                                                    </tr>
                                                </tbody>
                                            </table>

                                            <Accordion type="checkout">
                                            <Card title="Thanh toán khi nhận hàng" expanded={ true }>
                                                    Thanh toán trực tiếp khi nhận hàng.
                                                </Card>
                                                <Card title="Chuyển khoản ngân hàng" >
                                                    Tài khoản số:xxx yyyy zzzz
                                                    Ngân hàng: ACB Bank. 
                                                    Chủ Tài khoản : PHAN NGUYỄN KHÁNH VÂN. 
                                                    Nội dung:IDXABCDF
                                                </Card>

                                                

                                                
                                            </Accordion>
                                            <Link onClick={ checkoutHandle } to={ `#` } className="btn btn-outline-primary-2 btn-order btn-block">
                                            {loading?<ReactLoading type={"spin"} color={"red"} height={30} width={30} />:null}
                                            <span style={{marginLeft:20}}  className="btn-text">Nhấn để hoàn tất đặt hàng</span>
                                                <span className="btn-hover-text">Nhấn để hoàn tất đặt hàng</span>
                                            </Link>
                                            {/* <input  type="submit" className="btn btn-outline-primary-2 btn-order btn-block">
                                                <span className="btn-text">Nhấn để hoàn tất đặt hàng</span>
                                                <span className="btn-hover-text">Nhấn để hoàn tất đặt hàng</span>
                                            </input> */}
                                        </div>
                                    </aside>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export const mapStateToProps = ( state ) => ( {
    cartlist: state.cartlist.cart,
    total: getCartTotal( state.cartlist.cart ),
    shipping: state.cartlist.shipping
} )

export default connect( mapStateToProps,{clearCart} )( Checkout );